@import url("https://fonts.googleapis.com/css2?family=Teko&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bangers&display=swap");

body {
  margin: 0;
  padding: 0;
  font-family: "Teko", sans-serif;
}

h1 {
  font-family: "Bangers", cursive;
  letter-spacing: 0.2rem;
  font-size: 3rem;
  text-align: center;
}

p {
  font-size: 2rem;
  text-align: center;
}

.mainContainer {
  flex: 1 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
  overflow: hidden;
}

.mainContainer button {
  font-size: 2rem;
  padding: 0rem 1rem;
  border-radius: 0.3rem;
  opacity: 0;
  border: none;
  background-color: #239460;
  color: whitesmoke;
  font-family: "Teko", sans-serif;
  animation: fadein 1s;
  animation-delay: 1.5s;
  animation-fill-mode: forwards;
}

.mainContainer button:active {
  background-color: #c43d7e;
  transform: translate(0.2rem, 0.2rem);
}

.bgContainer {
  display: flex;
  position: fixed;
  height: 100vh;
  width: 100vw;
  z-index: -1;
  background: lightgrey;
}

.bgContainer img {
  opacity: 0.5;
  width: 100%;
  object-fit: cover;
  object-position: 70% 80%;
}

.mainImage {
  display: flex;
  justify-content: center;
  max-width: 15rem;
  max-height: 15rem;
  margin: 1rem;
  border: solid black 1rem;
  border-radius: 3rem;
  overflow: hidden;
  background: black;
}

.mainImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 20%;
}

.titleAnim {
  transform: translateX(-95vw);
  animation-duration: 1s;
  animation-name: slidein;
  animation-delay: 1s;
  animation-fill-mode: forwards;
}

@keyframes slidein {
  from {
    transform: translateX(-95vw);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media (orientation: landscape) {
}
