.mainContainer {
  flex: 1 100%;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  margin-bottom: 1rem;
  padding: 1rem;
}

.mainContainer h2 {
  font-size: 4rem;
  margin: 0;
}

.answerContainer {
  flex: 1 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: solid black 0.2rem;
  width: 50%;
}

.answerContainer h2 {
  font-size: 1.25rem;
  margin: 0;
  padding-top: 0.5rem;
}

.correctMark {
  background: green;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  margin-left: 1rem;
}

.incorrectMark {
  background: red;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  margin-left: 1rem;
}
