.mainContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.mainContainer > * {
  margin: 0;
}

.mainContainer h2 {
  margin: 0;
  text-align: center;
  font-size: 3rem;
}

.slideIn {
  transform: translateX(-95vw);
  animation-duration: 1s;
  animation-name: slidein;
  animation-delay: 0s;
  animation-fill-mode: forwards;
}

.slideOut {
  animation-duration: 1s;
  animation-name: slideout;
  animation-delay: 0s;
  animation-fill-mode: forwards;
}

@keyframes slidein {
  from {
    transform: translateX(-100vw);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes slideout {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(100vw);
  }
}
