.mainContainer {
  flex: 1 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
  border-radius: 1.5rem;
  padding: 1rem;
}

.mainContainer h2 {
  font-size: 2rem;
  margin: 0;

  letter-spacing: 0.2rem;
}

.buttonsContainer {
  flex: 1 100%;
  width: 100%;
  max-height: 50vh;
  max-width: 50vh;
  display: flex;
  justify-content: space-evenly;
}
