.mainContainer {
  display: flex;
  margin: 0rem 1.5rem 1rem 1.5rem;
  height: 35vh;
  width: 35vh;
  border: solid black 0.5rem;
  border-radius: 3rem;
  overflow: hidden;
  background: white;
}

.mainContainer img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: 50% 50%;
}

